import React, { Component } from "react";
import posed from "react-pose";

const El = posed.div({
  enter: {
    y: 0,
    opacity: 1
  },
  exit: {
    y: -50,
    opacity: 0
  }
});

const Container = posed.div({
  enter: {
    staggerChildren: 50
  }
});

const Pf = (props) => {
  const imgStyle = {
    height: '250px',
  }
  return (
    <div className="col-6 col-sm-4 col-md-3 mt-2">
      <div className="card">
        <img className="card-img-top bg-dark" src={props.src} alt={props.alt} style={imgStyle} />
        <div className="card-img-overlay">
          <div title="Tech used" className="tech rounded py-3 mt-4">
            <div>{props.tech}</div>
          </div>
          <a href={props.linkDemo}>
            <div
              title={(props.linkDemo !== '#') ? 'Demo available': 'Demo is Comming Soon'}
              className="demo rounded py-3"
              >
              <div>DEMO</div>
            </div>
          </a>
          <a href={props.linkDownload}>
            <div title={props.title} className="download rounded py-3">
              <div><i className="fa fa-heart" aria-hidden="true"></i> {(props.name === 'rankingForki') ? 'Sold out': 'Download'}</div>
            </div>
          </a>
        </div>
        <div className="card-header">
          <h6 className="card-title font-weight-bold">{props.title}</h6>
        </div>
      </div>
    </div>
  );
}

export default class Portfolios extends Component {
  constructor(props) {
    super(props);

    this.state = {
      pfImgJv: require("../assets/images/pf/InvMansa.png"),
      swalShow: false,
    }
  }

  render() {
    const {portfolios, menus} = this.props.data.lang
    const Items = portfolios.items.names.map((name, key) => (
      <Pf
        key={key}
        tech={portfolios.items.tech[key]}
        linkDemo={portfolios.items.linkDemos[key]}
        linkDownload={portfolios.items.linkDownloads[key]}
        src={require('../assets/images/pf/' + portfolios.items.imgNames[key])}
        alt={portfolios.items.titles[key]}
        title={portfolios.items.titles[key]} 
        name={name} 
        />
    ));

    return (
      <Container>
        <div id="portfolios">
          <El>
            <div className="header">
              <h1>{menus[3]}</h1>
            </div>
          </El>
          <El>
            <div className="content">
              <div className="row">
                {Items}
              </div>
                <div className="alert alert-info mt-2">
                  <span className="text-info">
                    Note : {portfolios.note}
                  </span>
                </div>
            </div>
          </El>
        </div>
      </Container>
    );
  }
}
